@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
body {
  margin: 0;
  font-family: 'Nunito', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  background: #F3F3F3;
  box-sizing: border-box;
  display: flex;
  text-align: center;
}
.navbar {
    background-color: darkorchid;
    overflow: hidden;
    position: fixed;
    top: 0;
    width: 100%;
    padding: auto;
    z-index: 10;
}

.nav-items {
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin: 0 20px;
}

.navbar img {
    width: 100px;
}

.navlinks {
    display: flex;
    justify-content: space-around;
}

.navlinks Link{
    text-decoration: none;
}

.navlinks li{
    list-style-type: none;
    margin: 0 20px;
    color: white;
    
}

.navbar input {
    align-items: flex-start;
    background-color:darkorchid;
    height: 20px;
    padding: 10px;
    border: none;
    border-bottom: 2px solid #d3d3d3;
    transition: 0.3s ease-in;
}

::-webkit-input-placeholder {
    color: rgb(235, 235, 235);
}

:-ms-input-placeholder {
    color: rgb(235, 235, 235);
}

::-ms-input-placeholder {
    color: rgb(235, 235, 235);
}

::placeholder {
    color: rgb(235, 235, 235);
}

.navbar input:focus {
    outline: none;
    color: white;
    /* border-bottom: 2px solid #d3d3d3; */
}

@media only screen and (max-width: 768px) {

    /* .navbar {
        height: 50px;
    } */

    .nav-items {
        display: flex;
        justify-content: space-evenly;
    }

    .navlinks {
        display: block;
    }
}
.types {
    display: block;
    margin-top: 4rem;
}

.types-container {
   display: flex;
   flex-wrap: wrap;
   justify-content: center;
   align-items: center;
}
.card-container {
    display: block;
    margin: 1rem;
    width: 300px;
    box-shadow: 0 5px 5px grey;
    border-radius: 10px;
    transition: 0.2s ease-in-out;
    cursor: pointer;
    background-color: rgb(247, 166, 166);
    
}

.card-container:hover {
    transform: scale(1.05);
    box-shadow: 0 9px 7px grey;
    z-index: 0;
}

.card-container:active {
    transform: scale(0.98);
}

.card-header {
    border-radius: 10px 10px 0 0;
    background-color: white;
}

.img {
    border-radius: 10px 10px 0 0;
    width: auto;
    max-width: 300px;
    height: 300px;
}

.name {
    background-color: rgb(173, 173, 173);
    color:white;
    padding: 1px;
    margin: 0;
}

.detail {
    padding: 1rem;
    background-color: lightblue;
    border-radius: 0 0 10px 10px;
}
.stats {
    display: block;
    margin-top: 4rem;
}

.stats-container {
   display: flex;
   flex-wrap: wrap;
   justify-content: center;
   align-items: center;
}
.pokemon-list {
    display: flex;
    flex-wrap: wrap;
    margin-top: 4rem;
    justify-content: center;
}

a {
    text-decoration: none;
}

